import React, { useState } from 'react';

interface ExamplesMenuProps {
  onLoadExample: (exampleName: string) => void;
  examples: Record<string, any>;
  onClose: () => void;
}

const ExamplesMenu: React.FC<ExamplesMenuProps> = ({
  onLoadExample,
  examples,
  onClose
}) => {
  const [showExamplesMenu, setShowExamplesMenu] = useState(false);

  return (
    <>
      {/* Examples Menu Button */}
      <button
        onClick={() => setShowExamplesMenu(!showExamplesMenu)}
        className="p-2 bg-white/90 backdrop-blur-sm rounded-full shadow-lg hover:bg-white transition-colors"
        title="Examples"
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
        </svg>
      </button>

      {/* Overlay */}
      {showExamplesMenu && (
        <div 
          data-testid="overlay"
          className="fixed inset-0 z-[90]"
          onClick={() => {
            setShowExamplesMenu(false);
            onClose();
          }}
        />
      )}

      {/* Examples Menu */}
      {showExamplesMenu && (
        <div className="absolute bottom-12 left-4 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-4 w-64 z-[100]">
          <div className="flex flex-col space-y-2">
            {Object.keys(examples).map((exampleName) => (
              <button
                key={exampleName}
                onClick={() => {
                  onLoadExample(exampleName);
                  setShowExamplesMenu(false);
                  onClose();
                }}
                className="flex items-center space-x-2 p-2 hover:bg-gray-100 rounded-lg transition-colors w-full text-left"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 flex-shrink-0" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
                </svg>
                <span className="truncate">{exampleName}</span>
              </button>
            ))}
          </div>
        </div>
      )}
    </>
  );
};

export default ExamplesMenu; 