import React, { useEffect, useState } from 'react';

export type ToastType = 'info' | 'success' | 'error';

interface ToastProps {
  message: string;
  type: ToastType;
  persistent?: boolean;
  onClose: () => void;
}

const Toast: React.FC<ToastProps> = ({ message, type, persistent, onClose }) => {
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    if (!persistent) {
      const timer = setTimeout(() => {
        setIsVisible(false);
        setTimeout(onClose, 300); // Wait for fade out animation
      }, 3000);

      return () => clearTimeout(timer);
    }
  }, [persistent, onClose]);

  if (!isVisible) return null;

  const getTypeStyles = () => {
    switch (type) {
      case 'info':
        return 'bg-black/50 text-white';
      case 'success':
        return 'bg-green-600 text-white';
      case 'error':
        return 'bg-red-500/50 text-white';
      default:
        return 'bg-black/50 text-white';
    }
  };

  return (
    <div className={`fixed top-4 right-4 px-3 py-1 rounded-full text-sm z-50 transition-opacity duration-300 ${getTypeStyles()}`}>
      {message}
      {!persistent && (
        <button
          onClick={() => {
            setIsVisible(false);
            setTimeout(onClose, 300);
          }}
          className="ml-2 text-current hover:opacity-70 transition-opacity"
        >
          ×
        </button>
      )}
    </div>
  );
};

export default Toast; 