import React, { useEffect, useState, useRef } from 'react';
import { Node } from '../../../types';
import * as d3 from 'd3';
import { theme } from '../../../config/theme';

interface NodeFollowerProps {
  selectedNode: Node | null;
  svgRef: React.RefObject<SVGSVGElement>;
  simulationRef: React.RefObject<d3.Simulation<Node, any>>;
  nodeToDelete: Node | null;
  hasChildren: (nodeId: string) => boolean;
  onDeleteClick: (event: MouseEvent, node: Node) => void;
  onDeleteMouseLeave: () => void;
  newNodeName: string;
  onNewNodeNameChange: (newName: string) => void;
  onAddNode: () => void;
  nodeDescription: string;
  onNodeNameChange: (newName: string) => void;
  onNodeDescriptionChange: (newDescription: string) => void;
  nodeColors: Record<string, string>;
  inheritColor: boolean;
  onColorChange: (color: string) => void;
  onInheritColorChange: (inherit: boolean) => void;
}

const NodeFollower: React.FC<NodeFollowerProps> = ({
  selectedNode,
  svgRef,
  simulationRef,
  nodeToDelete,
  hasChildren,
  onDeleteClick,
  onDeleteMouseLeave,
  newNodeName,
  onNewNodeNameChange,
  onAddNode,
  nodeDescription,
  onNodeNameChange,
  onNodeDescriptionChange,
  nodeColors,
  inheritColor,
  onColorChange,
  onInheritColorChange,
}) => {
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [isAddMode, setIsAddMode] = useState(false);
  const [isEditMode, setIsEditMode] = useState(false);
  const [isColorMode, setIsColorMode] = useState(false);
  const [editName, setEditName] = useState('');
  const [editDescription, setEditDescription] = useState('');
  const animationFrameRef = useRef<number>();
  const svgRefRef = useRef<d3.Selection<SVGSVGElement, unknown, null, undefined>>();
  const inputRef = useRef<HTMLInputElement>(null);
  const editNameRef = useRef<HTMLInputElement>(null);
  const editDescriptionRef = useRef<HTMLTextAreaElement>(null);

  // Reset all mode states when selected node changes
  useEffect(() => {
    setIsAddMode(false);
    setIsEditMode(false);
    setIsColorMode(false);
    setEditName('');
    setEditDescription('');
  }, [selectedNode]);

  useEffect(() => {
    // Cleanup function that removes all event listeners
    const cleanup = () => {
      if (svgRefRef.current) {
        svgRefRef.current.on("zoom", null);
      }
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };

    // If no node is selected, just clean up and return
    if (!selectedNode || !svgRef.current) {
      cleanup();
      return;
    }

    const updatePosition = () => {
      const svg = d3.select(svgRef.current);
      const transform = d3.zoomTransform(svg.node() as SVGSVGElement);
      
      // Get the node's position in the transformed coordinate space
      const x = (selectedNode.x ?? 0) * transform.k + transform.x;
      const y = (selectedNode.y ?? 0) * transform.k + transform.y;
      
      setPosition({ x, y });
    };

    // Update position immediately
    updatePosition();

    // Store the SVG selection
    svgRefRef.current = d3.select(svgRef.current);

    // Update position on zoom
    svgRefRef.current.on("zoom", updatePosition);

    // Use requestAnimationFrame to periodically check for position updates
    const checkPosition = () => {
      updatePosition();
      animationFrameRef.current = requestAnimationFrame(checkPosition);
    };
    animationFrameRef.current = requestAnimationFrame(checkPosition);

    // Return cleanup function
    return cleanup;
  }, [selectedNode, svgRef]);

  // Focus the input when entering add mode
  useEffect(() => {
    if (isAddMode && inputRef.current) {
      inputRef.current.focus();
    }
  }, [isAddMode]);

  // Focus the name input when entering edit mode
  useEffect(() => {
    if (isEditMode && editNameRef.current) {
      editNameRef.current.focus();
    }
  }, [isEditMode]);

  // Add global ESC key handler
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        if (isAddMode) {
          setIsAddMode(false);
        } else if (isEditMode) {
          setIsEditMode(false);
        } else if (isColorMode) {
          setIsColorMode(false);
        }
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [isAddMode, isEditMode, isColorMode]);

  // Update local state when entering edit mode
  useEffect(() => {
    if (isEditMode && selectedNode) {
      setEditName(selectedNode.name);
      setEditDescription(nodeDescription);
    }
  }, [isEditMode, selectedNode, nodeDescription]);

  if (!selectedNode) return null;

  // Calculate vertical offset based on node size
  const verticalOffset = 60; // Increased distance below the node to ensure label visibility
  
  // Create array of controls with horizontal offsets
  const controlWidth = 32; // Width of each control
  const controlSpacing = 8; // Space between controls
  const totalWidth = (4 * controlWidth) + (3 * controlSpacing); // Width for all 4 controls in one row
  
  // Generate array of positions for first row (add, color)
  const firstRowControls = Array.from({ length: 2 }, (_, index) => {
    const startX = position.x - (totalWidth / 2) + (controlWidth / 2);
    const x = startX + index * (controlWidth + controlSpacing);
    return { x, y: position.y + verticalOffset };
  });

  // Generate array of positions for second row (edit, delete)
  const secondRowControls = Array.from({ length: 2 }, (_, index) => {
    const startX = position.x - (controlWidth + controlSpacing) / 2;
    const x = startX + index * (controlWidth + controlSpacing);
    return { x, y: position.y + verticalOffset + controlWidth + controlSpacing };
  });

  // Combine both rows
  const controls = [...firstRowControls, ...secondRowControls];

  const handleAddNodeKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && newNodeName.trim()) {
      e.preventDefault();
      onAddNode();
      // Keep the add mode active for adding more nodes
      if (inputRef.current) {
        inputRef.current.focus();
      }
    } else if (e.key === 'Escape') {
      setIsAddMode(false);
    }
  };

  const handleEditKeyPress = (e: React.KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    if (e.key === 'Escape') {
      setIsEditMode(false);
    }
  };

  const handleEditNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newName = e.target.value;
    setEditName(newName);
    onNodeNameChange(newName);
  };

  const handleEditDescriptionChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newDescription = e.target.value;
    setEditDescription(newDescription);
    onNodeDescriptionChange(newDescription);
  };

  // If in add mode, show the add child node interface
  if (isAddMode) {
    return (
      <div
        className="fixed pointer-events-auto z-50 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-3 text-sm"
        style={{
          left: position.x,
          top: position.y + verticalOffset,
          transform: 'translate(-50%, -50%)',
          minWidth: '200px'
        }}
      >
        <div className="space-y-3">
          <input
            ref={inputRef}
            type="text"
            value={newNodeName}
            onChange={(e) => onNewNodeNameChange(e.target.value)}
            onKeyDown={handleAddNodeKeyPress}
            placeholder="New node name"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          <button
            onClick={() => {
              if (newNodeName.trim()) {
                onAddNode();
                // Focus the input after adding
                if (inputRef.current) {
                  inputRef.current.focus();
                }
              }
            }}
            className={`w-full px-4 py-2 text-sm font-medium text-white rounded-lg shadow-lg transition-colors ${
              !newNodeName.trim()
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-blue-600 hover:bg-blue-700'
            }`}
          >
            Add Node
          </button>
          <div className="flex justify-between mt-2">
            <button
              onClick={() => setIsAddMode(false)}
              className="text-xs text-gray-500 hover:text-gray-700 flex items-center"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
              </svg>
              Close
            </button>
            <div className="text-xs text-gray-500">
              Press Esc to exit
            </div>
          </div>
        </div>
      </div>
    );
  }

  // If in edit mode, show the edit node interface
  if (isEditMode) {
    return (
      <div
        className="fixed pointer-events-auto z-50 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-3 text-sm"
        style={{
          left: position.x,
          top: position.y + verticalOffset,
          transform: 'translate(-50%, -50%)',
          minWidth: '200px'
        }}
      >
        <div className="space-y-3">
          <input
            ref={editNameRef}
            type="text"
            value={editName}
            onChange={handleEditNameChange}
            onKeyDown={handleEditKeyPress}
            placeholder="Node name"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          <textarea
            ref={editDescriptionRef}
            value={editDescription}
            onChange={handleEditDescriptionChange}
            onKeyDown={handleEditKeyPress}
            placeholder="Add a description..."
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 min-h-[100px]"
          />
          <div className="flex justify-between mt-2">
            <button
              onClick={() => setIsEditMode(false)}
              className="text-xs text-gray-500 hover:text-gray-700 flex items-center"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
              </svg>
              Close
            </button>
            <div className="text-xs text-gray-500">
              Press Esc to exit
            </div>
          </div>
        </div>
      </div>
    );
  }

  // If in color mode, show the color picker interface
  if (isColorMode) {
    return (
      <div
        className="fixed pointer-events-auto z-50 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-3 text-sm"
        style={{
          left: position.x,
          top: position.y + verticalOffset,
          transform: 'translate(-50%, -50%)',
          minWidth: '200px'
        }}
      >
        <div className="space-y-3">
          <div className="grid grid-cols-5 gap-2">
            {Object.values(theme.colors.node).map((color: string, index) => (
              <button
                key={index}
                onClick={() => {
                  onColorChange(color);
                  setIsColorMode(false);
                }}
                className={`aspect-square w-8 rounded-full border border-gray-200 shadow-sm hover:scale-110 transition-transform focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 ${
                  selectedNode && nodeColors[selectedNode.id] === color ? 'ring-2 ring-indigo-500 ring-offset-2' : ''
                }`}
                style={{ backgroundColor: color }}
              />
            ))}
          </div>
          <div className="flex items-center justify-center gap-2">
            <span className="text-sm text-gray-600">Propagate color</span>
            <button
              onClick={() => onInheritColorChange(!inheritColor)}
              className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors focus:outline-none ${
                inheritColor ? 'bg-blue-600' : 'bg-gray-200'
              }`}
            >
              <span
                className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                  inheritColor ? 'translate-x-6' : 'translate-x-1'
                }`}
              />
            </button>
          </div>
          <div className="flex justify-between mt-2">
            <button
              onClick={() => setIsColorMode(false)}
              className="text-xs text-gray-500 hover:text-gray-700 flex items-center"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-1" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
              </svg>
              Close
            </button>
            <div className="text-xs text-gray-500">
              Press Esc to exit
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <div
        className="fixed pointer-events-auto z-50 bg-white/30 backdrop-blur-sm rounded-full shadow-lg px-2 py-1"
        style={{
          left: position.x,
          top: position.y + verticalOffset,
          transform: 'translate(-50%, -50%)',
          display: 'flex',
          gap: '2px'
        }}
      >
        {controls.map((control, index) => {
          const isAddButton = index === 0;
          const isColorButton = index === 1;
          const isEditButton = index === 2;
          const isDeleteButton = index === 3;
          
          return (
            <button
              key={index}
              className={`w-10 h-10 flex items-center justify-center text-gray-700 hover:text-gray-900 transition-colors ${
                isDeleteButton ? (nodeToDelete?.id === selectedNode.id ? 'text-red-500 hover:text-red-500' : 'hover:text-red-500') : ''
              }`}
              onClick={
                isAddButton ? () => setIsAddMode(true) :
                isColorButton ? () => setIsColorMode(true) :
                isEditButton ? () => setIsEditMode(true) :
                isDeleteButton ? () => onDeleteClick(new MouseEvent('click'), selectedNode) : undefined
              }
              onMouseLeave={isDeleteButton ? onDeleteMouseLeave : undefined}
              title={
                isAddButton ? "Add child node" :
                isColorButton ? "Change node color" :
                isEditButton ? "Edit node" :
                isDeleteButton ? (nodeToDelete?.id === selectedNode.id 
                  ? "Click again to delete node with children" 
                  : (selectedNode.id === '0' ? "Reset to new central hub" : "Delete node and all its children")) : undefined
              }
              data-testid={isAddButton ? "add-button" : isColorButton ? "color-button" : isEditButton ? "edit-button" : isDeleteButton ? "delete-button" : `control-${index}`}
            >
              {isAddButton ? (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-11a1 1 0 10-2 0v2H7a1 1 0 100 2h2v2a1 1 0 102 0v-2h2a1 1 0 100-2h-2V7z" clipRule="evenodd" />
                </svg>
              ) : isColorButton ? (
                <div className="relative">
                  <div
                    className="w-6 h-6 rounded-full border-2 border-gray-200 shadow-sm"
                    style={{ backgroundColor: selectedNode ? (nodeColors[selectedNode.id] || (selectedNode.parentId === null ? theme.colors.node.green : theme.colors.node.blue)) : theme.colors.node.blue }}
                  />
                </div>
              ) : isEditButton ? (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                </svg>
              ) : isDeleteButton ? (
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                </svg>
              ) : null}
            </button>
          );
        })}
      </div>
      
      {/* Warning tooltip for delete confirmation */}
      {nodeToDelete?.id === selectedNode.id && (
        <div
          className="fixed pointer-events-none z-50 bg-gray-900 text-white text-xs py-1 px-2 rounded-full shadow-lg whitespace-nowrap"
          style={{
            left: position.x,
            top: position.y + verticalOffset - 30,
            transform: 'translate(-50%, -50%)'
          }}
          data-testid="delete-tooltip"
        >
          Click again to delete
        </div>
      )}
    </>
  );
};

export default NodeFollower; 