import React from 'react';

interface PhysicsSettings {
  linkDistance: number;
  chargeStrength: number;
  collisionRadius: number;
}

interface PhysicsPanelProps {
  physics: PhysicsSettings;
  onPhysicsChange: (newPhysics: PhysicsSettings) => void;
  onClose: () => void;
}

const defaultPhysics: PhysicsSettings = {
  linkDistance: 110,
  chargeStrength: -270,
  collisionRadius: 20
};

const PhysicsPanel: React.FC<PhysicsPanelProps> = ({ physics, onPhysicsChange, onClose }) => {
  const handleReset = () => {
    onPhysicsChange(defaultPhysics);
  };

  return (
    <div className="fixed bottom-4 right-4 bg-white rounded-lg shadow-lg p-4 w-64 z-50">
      <div className="flex justify-between items-center mb-4">
        <h3 className="text-lg font-medium">Physics Settings</h3>
        <div className="flex items-center space-x-2">
          <button
            onClick={handleReset}
            className="text-gray-500 hover:text-gray-700"
            title="Reset to defaults"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M4 2a1 1 0 011 1v2.101a7.002 7.002 0 0111.601 2.566 1 1 0 11-1.885.666A5.002 5.002 0 005.999 7H9a1 1 0 010 2H4a1 1 0 01-1-1V3a1 1 0 011-1zm.008 9.057a1 1 0 011.276.61A5.002 5.002 0 0014.001 13H11a1 1 0 110-2h5a1 1 0 011 1v5a1 1 0 11-2 0v-2.101a7.002 7.002 0 01-11.601-2.566 1 1 0 01.61-1.276z" clipRule="evenodd" />
            </svg>
          </button>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
            aria-label="Close"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
          </button>
        </div>
      </div>
      <div className="space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="linkDistance">
            Link Distance
          </label>
          <input
            id="linkDistance"
            type="range"
            min="50"
            max="200"
            value={physics.linkDistance}
            onChange={(e) => onPhysicsChange({ ...physics, linkDistance: Number(e.target.value) })}
            className="w-full"
          />
          <div className="text-sm text-gray-500 mt-1">{physics.linkDistance}</div>
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="chargeStrength">
            Charge Strength
          </label>
          <input
            id="chargeStrength"
            type="range"
            min="-500"
            max="0"
            value={physics.chargeStrength}
            onChange={(e) => onPhysicsChange({ ...physics, chargeStrength: Number(e.target.value) })}
            className="w-full"
          />
          <div className="text-sm text-gray-500 mt-1">{physics.chargeStrength}</div>
        </div>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1" htmlFor="collisionRadius">
            Collision Radius
          </label>
          <input
            id="collisionRadius"
            type="range"
            min="0"
            max="50"
            value={physics.collisionRadius}
            onChange={(e) => onPhysicsChange({ ...physics, collisionRadius: Number(e.target.value) })}
            className="w-full"
          />
          <div className="text-sm text-gray-500 mt-1">{physics.collisionRadius}</div>
        </div>
      </div>
    </div>
  );
};

export default PhysicsPanel; 